import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { AppSwitch } from '@coreui/react';

class Register extends Component {
  constructor(props) {
    super(props);
    this.registerUser = this.registerUser.bind(this);
    this.state = {
      username: '',
      age: '',
      email: '',
      firstname: '',
      lastname: '',
      mobile: '',
      region: '',
      pwd: '',
      repwd: '',
      title: '',
      admin: false,
    };
  }

  registerUser() {
    if (!isNaN(this.state.age)) {
      if (this.state.pwd === this.state.repwd) {
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ 
            username: this.state.username, 
            password: this.state.pwd,
            email: this.state.email, 
            firstname: this.state.firstname,
            lastname: this.state.lastname,
            mobile: this.state.mobile,
            region: this.state.region,
            age: this.state.age,
            admin: this.state.admin,
            title: this.state.title,
          })
        };
        fetch('https://missionaustralia-back.herokuapp.com/registerUser', requestOptions)
          .then(response => response.json())
          .then(resData => {
            console.log(resData);
            alert(resData.data.message)
          });
      } else {
        alert("Passwords doesn't match");
      }
    } else {
      alert("Incorrect age");
    }
  }

  updateInputValue(evt, val) {
    if (val === 1) {
      this.setState({
        username: evt.target.value
      });
    } else if (val === 2) {
      this.setState({
        age: evt.target.value
      })
    } else if (val === 3) {
      this.setState({
        email: evt.target.value
      })
    } else if (val === 4) {
      this.setState({
        firstname: evt.target.value
      })
    } else if (val === 5) {
      this.setState({
        lastname: evt.target.value
      })
    } else if (val === 6) {
      this.setState({
        mobile: evt.target.value
      })
    } else if (val === 7) {
      console.log(evt.target.value);
      
      this.setState({
        region: evt.target.value
      })
    } else if (val === 8) {
      this.setState({
        pwd: evt.target.value
      })
    } else if (val === 9) {
      this.setState({
        repwd: evt.target.value
      })
    } else if (val === 10) {
      this.setState({
        title: evt.target.value
      })
    } else if (val === 11) {
      this.setState({
        admin: evt.target.checked
      })
    }
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <Form>
                    <h1>Register</h1>
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-wrench"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      &nbsp;Admin Account : 
                      <AppSwitch className={'mx-1'} color={'success'} checked={this.state.admin} onChange={evt => this.updateInputValue(evt,11)} />
                    </InputGroup>
                    {this.state.admin === true?
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-expand"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="select" placeholder="Title" autoComplete="title" value={this.state.title} onChange={evt => this.updateInputValue(evt, 10)}>
                        <option>Doctor</option>
                        <option>Lawyer</option>
                        <option>Real Estate Agent</option>
                        <option>Banker</option>
                      </Input>
                    </InputGroup>
                    : null}
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Username" autoComplete="username" value={this.state.username} onChange={evt => this.updateInputValue(evt, 1)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-options"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Age" autoComplete="age" value={this.state.age} onChange={evt => this.updateInputValue(evt, 2)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>@</InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Email" autoComplete="email" value={this.state.email} onChange={evt => this.updateInputValue(evt, 3)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-user-o"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="First Name" autoComplete="firstname" value={this.state.firstname} onChange={evt => this.updateInputValue(evt, 4)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-user-circle"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" placeholder="Last Name" autoComplete="lastname" value={this.state.lastname} onChange={evt => this.updateInputValue(evt, 5)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-phone"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="mobile" placeholder="Mobile" autoComplete="mobile" value={this.state.mobile} onChange={evt => this.updateInputValue(evt, 6)} />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="fa fa-expand"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="select" placeholder="Region" autoComplete="region" value={this.state.region} onChange={evt => this.updateInputValue(evt, 7)}>
                        <option>NSW</option>
                        <option>Queensland</option>
                        <option>North Territory</option>
                        <option>Western Australia</option>
                        <option>South Australia</option>
                        <option>Victoria</option>
                        <option>Australian Capital Territory</option>
                        <option>Tasmania</option>
                      </Input>
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" placeholder="Password" autoComplete="new-password" value={this.state.pwd} onChange={evt => this.updateInputValue(evt, 8)} />
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock"></i>
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" placeholder="Confirm password" autoComplete="new-password" value={this.state.repwd} onChange={evt => this.updateInputValue(evt, 9)} />
                    </InputGroup>
                    <Button color="success" type="button" onClick={this.registerUser} block>Create Account</Button>
                    <div style={{ textAlign: 'center' }}><Link to='/login'> Login </Link></div>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
